<template>
  <div>
    <ul>
      <li v-for="(word,key) in showWordsData" :key="key">
        <div style="width:50%;float:left;margin:0 auto;text-align:center">{{word.English}}</div>
        <div
          style="width:50%;float:left;margin:0 auto;text-align:center"
          v-bind:class="ChineseTdClass"
          :style="{backgroundImage: 'url(' + Bg4TipUrl + ')' }"
        >{{word.Chinese}}</div>

        <div style="clear:both;"></div>
      </li>
    </ul>

    <div
      style="width:100px;height:100px;
    right: 20px;
    bottom: 70px;
    position: fixed;
    z-index: 1;
    margin:0 auto;
    text-align:center
    "
    >
      <button>第{{ page}}页</button>
      <br />
      <button @click="lastPage">上一页</button>
      <button @click="nextPage">下一页</button>
      <br />
      <button @click="useRand">
        <span v-if="config.isRand">乱序</span>
        <span v-else>定序</span>
      </button>
      <br />
      <button @click="showChinese">翻译</button>
      <br />
      <button @click="newRand">重新排序</button>
      <br />
    </div>
  </div>
</template>

<script>
import Bg4Tip from "@/assets/ChineseWordsBackground.png"
export default {
  name: "WordList",
  data() {
    return {
      Bg4TipUrl:Bg4Tip,
      wordsData: [
        {
          English: "English",
          Chinese: "汉语"
        }
      ],
      showWordsData: [
        {
          English: "English",
          Chinese: "汉语"
        }
      ],
      config: {
        page: 1,
        isRand: true,
        hideChinese: true
      },
      page: 1
    };
  },
  computed: {
    ChineseTdClass: function() {
      return {
        ChineseBgHover: this.config.hideChinese
      };
    }
  },
  methods: {
    MUnknow: function(arrTmp) {
      var arr = arrTmp.concat();
      var newArr = [];
      for (var i = 0; i < arr.length; i++) {
        var index = Math.floor(Math.random() * arr.length);
        newArr.push(arr[index]);
        arr.splice(index, 1);
      }
      var res = [...newArr, ...arr];
      return res;
    },
    getWordList: function(page) {
      if (typeof page == "undefined") {
        page = 1;
      }
      this.$axios
        .get("/wordList/" + page)
        .then(
          res => (this.showWordsData = this.wordsData = res.data.value.wordlist)
        )
        .catch(err => {
          console.log("异常", err);
        });
    },
    lastPage: function() {
      if (this.page > 1) {
        this.getWordList((this.page -= 1));
      }
    },
    nextPage: function() {
      if (this.page < 5) {
        this.getWordList((this.page += 1));
      }
    },
    newRand: function() {
      this.showWordsData = this.MUnknow(this.wordsData);
      this.config.isRand = true;
    },
    showChinese: function() {
      this.config.hideChinese = !this.config.hideChinese;
    },
    useRand: function() {
      this.config.isRand = !this.config.isRand;
      if (this.config.isRand) {
        this.showWordsData = this.MUnknow(this.wordsData);
      } else {
        this.showWordsData = this.wordsData;
      }
    }
  },
  created() {
    this.getWordList(this.page);
  },
  watch: {
    wordsData: function(oldV, newV) {}
  }
};
</script>

<style lang="css" scoped>
.ChineseBgHover {
  color: rgba(0, 0, 0, 0);
  background: repeat;
  background-size: cover;
  background-position: center center;
}

.ChineseBgHover:hover {
  color: #797268;
  background: url("");
}
</style>


