<template>
  <div id="app">
    <mt-header fixed title="皮" style="background-color:black">
      <mt-button icon="more" slot="right"></mt-button>
    </mt-header>

    <div style="padding-top:40px;padding-bottom:300px">
      <router-view />
    </div>
    <mt-tabbar fixed v-model="selected">
      <mt-tab-item id="wordList">
        <img slot="icon" src="./assets/wordlist.png" />
        WordList
      </mt-tab-item>
      <mt-tab-item id="about">
        <img slot="icon" src="./assets/about.png" />
        about
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      selected: ""
    };
  },
  watch: {
    selected: function(newV, oldV) {
      if (newV !== oldV) {
        
        this.$router.push({ name: this.selected });
      }
    }
  }
};
</script>

<style lang="css" scoped>
</style>



